import { Box, Heading, Image } from "@chakra-ui/react";

export default function Icons({ urls, title }) {
  return (
    <Box
      sx={{
        width: "150px",
        textAlign: "center",
        margin: "1rem",
      }}
    >
      <Image
        sx={{ boxShadow: "rgba(149, 157, 165, 0.2) 0px 8px 24px" }}
        src={urls}
      />
      <Heading as="h4" fontSize="2xl" mt={3}>
        {title}
      </Heading>
    </Box>
  );
}
